/* ------------------------------------*\
    #HEADINGS
\*------------------------------------ */

h1,
h2,
h3,
h4,
h5,
h6 {
  color: $color-title;
  font-family: $title-font-family;
  >a {
    color: inherit;
  }
}

h1,
.h1 {
  font-size: 1.375rem;

  @include min-screen($breakpoint-tiny) {
    font-size: 1.5rem;
  }
}

h2,
.h2 {
  font-size: 1.1rem;

  @include min-screen($breakpoint-tiny) {
    font-size: 1.375rem;
  }
}

h3,
.h3 {
  font-size: 0.9rem;
  @include min-screen($breakpoint-tiny) {
    font-size: 1.1rem;
  }
}

h4,
.h4 {}

h5,
.h5 {}

h6,
.h6 {}
